import { useEffect, useState } from "react";
import { Table, MessagePlugin } from "tdesign-react";
import { apiSysConfig } from "../../api/api"

export type systemlistType = {
  id: string | number;
  name: string;
  ak: string;
  sk: string;
  type: string;
}[];

export type itemListType = {
  serverName: string;
  servers: systemlistType;
}[];

function SystemSet(props: any) {
  const [itemList, setItemList]  = useState([])

  useEffect(()=> {
    getInteData()
  },[])

  function getInteData(){
    apiSysConfig().then((res:any) => {
      const { data } = res
      if (res.code === 0) {
        setItemList(data)
      } else {
        MessagePlugin.error(res.msg ?? "请求出错！");
      }
    })
  }

  return (
    <div className="systemSet container" key="sts">
      {itemList.map((data: any, index: number) => {
        return <MapList data={data} key={index}></MapList>;
      })}
    </div>
  );
}

function MapList(props: any) {
  const { data } = props;
  return data ? (
    <div className="mapList">
      <div className="title"><span>{data.serverName}</span> {data.enable ? (<span className="off"><em></em>开启</span>) : (<span className="on"><em></em>关闭</span>)}</div>
      <div className="cont">
        <Table 
          data={data.servers} 
          columns={[
            {
              align: 'left',
              className: 'row',
              ellipsis: true,
              colKey: 'id',
              cell({ row }) {
               return row.id ? row.id : '--'; 
              },
              title: '应用ID',
            },
            {
              align: 'left',
              className: 'test',
              ellipsis: true,
              colKey: 'name',
              title: '应用名称',
            },
            {
              align: 'left',
              className: 'test4',
              ellipsis: true,
              colKey: 'ak',
              title: 'AK',
            },
            {
              align: 'left',
              className: 'test4',
              ellipsis: true,
              colKey: 'sk',
              title: 'SK',
              cell({ row }) {
                return row.sk ? row.sk : '--'; 
               },
            },
            {
              align: 'left',
              className: 'test4',
              ellipsis: true,
              colKey: 'type',
              title: '类型',
            },
          ]} 
          rowKey="index" 
          verticalAlign="top"
          bordered
          hover
        ></Table>
      </div>
    </div>
  ) : null;
}

export default SystemSet;
